import styled from 'styled-components';
export const HeaderTag = styled.header`
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    border-bottom: 1px solid #e9ecef;
    & > ul {
        /* pc ÎìúÎ°≠Îã§Ïö¥ÏùÄ Î™®Î∞îÏùºÏóêÏÑú Î≥¥Ïù¥ÏßÄ ÏïäÍ≤å ÏÑ§Ï†ï*/
        display:none;
    }
    & button + a {
        /* Ïù¥ÎØ∏ÏßÄ */
        display:none;
    }
    /* --- desktop --- */
    @media ${({ theme: { desktop } }) => desktop} {
        border-bottom: 1px solid #e9ecef;
        justify-content: space-around;
        padding: 0;
        & > button {
            display:none;
        }
        & button + a {
            display: block;
        }
        & > ul {
            /* ÎìúÎ°≠Îã§Ïö¥ menuItmes.jsx*/
            display:flex;
        }
    }
`
export const UserInfoWrapper = styled.div`
    & button {
        font-size : 18px;
    }
`